.container{
    margin: 50px 0px;
}

.card{
    margin: 0px 2% !important;
}

.infected{
    border-bottom: 20px solid rgba(0,0,255,0.9);
}


.recovered{
    border-bottom: 20px solid rgba(0,255,0,0.9);
}


.deaths{
    border-bottom: 20px solid rgba(255,0,0,0.9);
}

@media (max-width: 770px){
    .card{
        margin: 2% 0 !important;
    }
}
