body{
    background-image: linear-gradient(to right,rgba(255,0,0,0.6) , rgba(255,255,0,0.6));
}

.container{
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
}

.images{
    width: 370px;
    margin-left: 20%;
}

@media only screen and (max-width: 770px){
    .container{
        margin: 0 10%;
    }
    .images{
        width:250px;
        margin-left: 20px;
    }
}