.container{
    margin: auto;
    display: flex;
    justify-content: center;
    align-content: center ;
    width: 80%;
}

@media only screen and (max-width: 770px){
    .container{
        margin: auto;
        display: flex;
        justify-content: center;
        align-content: center ;
        width: 300px;
        height: 300px;
    }
}


